import { useCallback, useRef } from "preact/hooks";
import type { VIEWS as _VIEWS } from "../Common/CommonModel";
import { Header } from "../components/app/Header";
import { ScrollToLeft, scrollToShow, ScrollToTop } from "../components/app/ScrollToTopOrLeft";
import { type CoordinatorCardView as _CoordinatorCardView } from "../components/coordinator/CoordinatorCardView";
import type { CoordinatorFilterZone as _FilterZone } from "../components/coordinator/CoordinatorFilterZone";
import type { CoordinatorTableView as _CoordinatorTableView } from "../components/coordinator/CoordinatorTableView";
import { MissionCard } from "../components/coordinator/MissionCard";
import { automaticWakeLock } from "../components/WakeLockButton";
import { openedMissionIndex, tripleExampleMissions } from "../context/data";
import { globalState } from "../context/GlobalState";
import { FullViewport, Overlap, Vertical } from "../utils/ComponentToolbox";
import { CoordinatorCardView, CoordinatorTableView } from "./exports_";

automaticWakeLock();

/**
 * Renders the coordinator page, with the {@link Header | `Header`}, the {@link _CoordinatorCardView | `Card view`} *(lazy loaded)* \
 * or the {@link _CoordinatorTableView | `Table view`} *(lazy loaded)* according to the {@link _VIEWS | `view`} *(edited in the {@link _FilterZone | `FilterZone`})*. \
 * It also displays a {@link ScrollToTop | `scroll to top button`} and a {@link ScrollToLeft | `scroll to left button`}. \
 * The {@link automaticWakeLock | `automatic wake lock`} is also enabled.
 * @returns The rendered coordinator page.
 */
export const CoordinatorPage = () => {
	const scrollAreaRef = useRef<HTMLDivElement>(null);
	const onScroll = useCallback(() => {
		scrollToShow.x.value = (scrollAreaRef.current?.scrollLeft ?? 0) > 90;
		scrollToShow.y.value = (scrollAreaRef.current?.scrollTop ?? 0) > 45;
	}, [scrollAreaRef]);
	const scrollToTop = useCallback(() => scrollAreaRef.current?.scrollTo({ top: 0, behavior: "smooth" }), [scrollAreaRef]);
	const scrollToLeft = useCallback(() => scrollAreaRef.current?.scrollTo({ left: 0, behavior: "smooth" }), [scrollAreaRef]);

	return (
		<FullViewport>
			<Vertical height="100%">
				<Overlap height="100%">
					<div id={"scroll-root"} style={{ overflow: "auto" }} ref={scrollAreaRef} onScroll={onScroll}>
						<Header />
						{globalState.view.value === "card" && <CoordinatorCardView />}
						{globalState.view.value === "table" && <CoordinatorTableView />}
					</div>
					{openedMissionIndex.value !== -1 && (
						<div style={`height: 100%; z-index: 250`}>
							<MissionCard
								mission={tripleExampleMissions.value[openedMissionIndex.value]}
								index={openedMissionIndex.value}
								isFull
							/>
						</div>
					)}
				</Overlap>
			</Vertical>
			<ScrollToTop scrollToTop={scrollToTop} />
			<ScrollToLeft scrollToLeft={scrollToLeft} />
		</FullViewport>
	);
};
