import { Affix, Button, Transition, rem } from "@mantine/core";
import { signal } from "@preact/signals";
import { ArrowLeft, ArrowUp } from "lucide-react";
import { compactXsSm } from "../../context/GlobalState";

/**
 * The signal to show the scroll to left and scroll to top buttons (respectively x and y)
 */
export const scrollToShow = {
	/** The signal to show the scroll to left button */
	x: signal(false),
	/** The signal to show the scroll to top button */
	y: signal(false),
};

/**
 * The scroll to top button, affixed to the bottom right corner of the screen
 * @param scrollToTop the function to scroll to top
 * @returns the scroll to top button
 */
export const ScrollToTop = ({ scrollToTop }: { scrollToTop: () => void }) => (
	<Affix position={{ bottom: 20, right: 20 }}>
		<Transition transition="slide-up" mounted={scrollToShow.y.value}>
			{(transitionStyles) => (
				<Button
					leftSection={<ArrowUp style={{ width: rem(16), height: rem(16) }} />}
					style={{ ...transitionStyles, border: "1px solid #000000" }}
					variant="white"
					size={compactXsSm.value}
					onClick={scrollToTop}
				>
					Scroll to top
				</Button>
			)}
		</Transition>
	</Affix>
);

/**
 * The scroll to left button, affixed to the bottom right corner of the screen, just above the scroll to top button if both are shown
 * @param scrollToLeft the function to scroll to left
 * @returns the scroll to left button
 */
export const ScrollToLeft = ({ scrollToLeft }: { scrollToLeft: () => void }) => (
	<Affix position={{ bottom: scrollToShow.y.value ? 50 : 20, right: 20 }}>
		<Transition transition="slide-up" mounted={scrollToShow.x.value}>
			{(transitionStyles) => (
				<Button
					leftSection={<ArrowLeft style={{ width: rem(16), height: rem(16) }} />}
					style={{ ...transitionStyles, border: "1px solid #000000" }}
					variant="white"
					size={compactXsSm.value}
					onClick={scrollToLeft}
				>
					Scroll to left
				</Button>
			)}
		</Transition>
	</Affix>
);
