import { Button, Image, Paper } from "@mantine/core";
import Chabe_logo_white from "../../assets/Chabe_logo_white.png";
import { widthSizeObj } from "../../utils/commonUtils";
import { Horizontal } from "../../utils/ComponentToolbox";
import { DarkModeButton } from "../DarkModeButton";

const headerSize = 3.5;
const headerScale = 6;

/**
 * @notExported
 * The Chabe logo
 * @returns the Chabe logo
 */
const ChabeLogo = () => <Image w={widthSizeObj(headerSize, headerScale)} alt="Logo Chabe" src={Chabe_logo_white} />;

/**
 * @notExported
 * The logout button
 * @returns the button to logout, redirecting to the login page on success, displaying a modal on failure to force the user to logout
 */
const LogoutButton = () => {
	return (
		<Button
			color="chabe-red"
			style={{ fontSize: widthSizeObj(2.8, headerScale), height: widthSizeObj(4.5, headerScale), fontWeight: 500 }}
		>
			Logout
		</Button>
	);
};

const email = "email@test.com";

/**
 * The header of the app, with the {@link ChabeLogo | `Chabe logo`}, the `company name`, the {@link DarkModeButton | `dark mode button`},
 * the `user email` and the {@link LogoutButton | `logout button`}.
 * @returns the header component
 */
export const Header = () => (
	<Paper w="100%" bg="chabe-blue" px={16} style={{ position: "sticky", left: 0 }}>
		<Horizontal justifyContent="space-between">
			<Horizontal>
				<ChabeLogo />
				<h2 style={{ margin: `0 ${widthSizeObj(1)}`, fontSize: widthSizeObj(headerSize, headerScale), color: "white" }}>CHABE</h2>
				<DarkModeButton useTransition={false} />
			</Horizontal>
			<h2 style={{ fontSize: widthSizeObj(headerSize, headerScale), color: "white" }}>{email}</h2>
			<LogoutButton />
		</Horizontal>
	</Paper>
);
